.backdrop {
  width: 100%;
  height: 100%;
  background-color: rgba(9, 9, 9, .3);
  position: fixed;
  top: 0;
  left: 0;
}

.modal {
  width: 350px;
  background-color: #fff;
  position: absolute;
  top: 50%;
  left: 50%;
  transform: translate(-50%, -50%);
}

