  .backdrop {
    position: fixed;
    top: 0;
    left: 0;
    background-color: rgba(9, 9, 9, 0.3);
    width: 100%;
    height: 100%;
  }
  
  .modal {
    position: absolute;
    top: 50%;
    left: 50%;
    transform: translate(-50%, -50%);
    background-color: white;
    width: 350px;
   }

